@font-face {
    font-family: 'alethia_proregular';
    src: url('./alethiapro-regular-webfont.eot');
    src: url('./alethiapro-regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('./alethiapro-regular-webfont.woff2') format('woff2'),
         url('./alethiapro-regular-webfont.woff') format('woff'),
         url('./alethiapro-regular-webfont.ttf') format('truetype'),
